import React from 'react';
import { Link } from 'react-router-dom';

const PersonImgGroup = ({ name, route, info, onGalleryNameClcik }) => {
  return (
    <div className="col-md-4 pt-5 pl-5 pr-5 ">
      <Link
        to="/artist"
        onClick={() => {
          onGalleryNameClcik(null, name);
        }}
      >
        <div className="img-wrap">
          <img
            src={require(`../../img/person/${name}.jpg`)}
            className="img-fluid rounded-circle"
            alt=""
          />
          <div className="circle-dark-overlay d-flex justify-content-center align-items-center">
            <h3>{name}</h3>
          </div>
        </div>
      </Link>
    </div>
  );
};

export default PersonImgGroup;



