import React from 'react';

export default () => {
  return (
    <div>
      {/* Newsletter Section */}

      <section id="contact" className="bg-dark text-center text-white py-3 ">
        <div className="container">
          <div className="row">
            <div className="col contact">
              <h3 className="pb-1 contact">Contact</h3>
              <p>如遇到任何问题，请发送邮箱至：</p>
              <p className="p-0">tomscavo960@gmail.com</p>
            </div>
          </div>
        </div>
      </section>
      <footer id="main-footer" className="text-center p-4">
        <div className="container">
          <div className="row">
            <div className="col align-center">
              <p className="pt-3">
                Copyright &copy; {new Date().getFullYear()} 破壁点津
              </p>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
};



