import React from 'react';

const Plan = () => {
  window.scrollTo(0, 0);
  return (
    <div>
      <img
        src={require('../../img/cover.jpg')}
        alt=""
        className="img-fluid w-100 py-5"
      />
      <h3 className="text-center p-2">前言</h3>

      <div className="container p-5 mb-5">
        <div className="row">
          <div className="col-md-10 m-auto">
            <p>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;当谈及艺术时，人们习惯于去说“艺术无疆”，我时常思考，何为“艺术无疆”？艺术又何以无疆？当我们从大的格局俯瞰艺术世界，艺术确能超越时间的深度与空间的广度，纵古今、连中外、无远弗届；但若将范围缩小至艺术群体和艺术家个体，同样的问题似乎有了不同的答案
            </p>
            <p>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;参观了几次美院毕业展和跟美院的同学交流之后，这个问题再次浮现脑海，我尝试着为美院的青年学子提供一个自我展现的平台并与独立艺术家交流的机会，因而有了本次的展览。展览有幸邀请到湖南省画院的田绍登老师和付红老师，以及中国美术学院的学子王子豪、杨任伟、张鹏、胡维桢、段美婷、高泽、黄己榕。
            </p>
            <p>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;展览的主题“破壁点津”也源于这个初衷。“破壁”即打破不同年龄、不同身份的界限，组织美院学子与艺术家前辈共同展览、互通有无，让艺术家们保持活性，让青年学子吸收营养。“桃源何处是，游子正迷津。”艺术桃源，远游在外的学子们心之所向，他们正渴望老师们指点迷津，走好自己的艺术之路。
            </p>
            <p>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;文化自信的重要性多次被重提，要坚持走文化自信的道路，坚守艺术理想，勇于创新创造。这对广大的艺术工作者来说，是机遇，更是挑战。青年艺术学子也要向艺术家前辈学习，怀揣使命感与责任感，在艺术道路上继往开来。
            </p>
            <p>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;“文变染乎世情，兴废系乎时序。”别处即此处，他时即此时。这就是艺术创作最好的时代。
            </p>
            <blockquote className="text-right">——李思诗</blockquote>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Plan;



