import React from 'react';
import classnames from 'classnames';

const GalleryImgGroup = ({ name, index, person }) => {
  return (
    <div className="row px-5 py-4 gallery-imgs ">
      <div id="artWorks" className="col-md-9 m-auto p-none">
        <img
          src={require(`../../img/学生作品/1/${name}${index}.JPG`)}
          alt=""
          className="img-fluid"
        />
        <div className="dark-overlay img-info">
          <div className="h-100 d-flex justify-content-center align-items-center">
            <div className="text-center">
              <div className="img-info-border">
                <p className=" pt-3">{person.imgInfo[index - 1]}</p>
                <p>{person.imgSize[index - 1]}</p>
              </div>
              <p className=" py-3">{person.imgFooter[index - 1]}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GalleryImgGroup;



