import React from 'react';
import classnames from 'classnames';
import AnchorLink from 'react-anchor-link-smooth-scroll';

const PersonNavName = ({ name, nameActive, onGalleryNameClcik }) => {
  return (
    <div id="PersonNavName" className="border-bottom border-secondary">
      <AnchorLink
        href="#artWorks"
        className={classnames('px-4 py-2 text-muted  d-block ', {
          'bg-mb-light text-dark': nameActive
        })}
        onClick={e => {
          onGalleryNameClcik(e, name);
        }}
      >
        <div className={classnames({ 'text-black': nameActive })}>{name}</div>
      </AnchorLink>
    </div>
  );
};

export default PersonNavName;



