import React from 'react';
import { Link } from 'react-router-dom';

const CardProductGroup = ({ name, route, info, onGalleryNameClcik }) => {
  return (
    <div className="card">
      <div className="card-img-wrap bg-light">
        <div className="d-flex justify-content-center h-100">
          <img
            className="card-img-top m-auto"
            src={require(`../../img/学生作品/0/${name}.JPG`)}
            alt=""
          />
        </div>
      </div>

      <div className="card-body text-center">
        <h4 className="card-title p-3">
          {name}
          作品集
        </h4>

        <Link
          to="/gallery"
          onClick={e => {
            onGalleryNameClcik(null, name);
          }}
          className="btn btn-outline-danger"
        >
          查看详情
        </Link>
      </div>
    </div>
  );
};

export default CardProductGroup;



