import React from 'react';

const Plan = () => {
  window.scrollTo(0, 0);
  return (
    <div>
      <img
        src={require('../../img/cover5.jpg')}
        alt=""
        className="img-fluid w-100 py-5"
      />

      <h3 className="text-center p-5 mb-5">
        2018年大学生科技创新活动计划（暨新苗人才计划）——“不忘初心，一脉相承”主题展览项目
      </h3>

      <div className="container pb-5 mb-5">
        <h5>(一) 项目介绍</h5>
        <p>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;习近平总书记在十九大报告中向全党全国人民发出了“坚定文化自信，推动社会主义文化繁荣兴盛”的伟大号召。作为艺术学院的学子，感受颇深。文化命运与国家命运相牵，流淌着五千多年的中华文化徜徉在中华大地上，作为新一代的文艺青年，应该担负起凝聚民族复兴之魂的责任。
        </p>
        <p>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;身在美院，我们更希望用艺术的方式呈现我们对十九大精神的理解以及对中华民族文化复兴的奋斗并且我们相信我们有能力有平台。我们选择用展览方式来呈现，展览包括：绘画作品（国画、书法等）、艺术家纪录片，后期可深入进展时考虑添加装置，动画等展览内容。展览包含专职艺术家，青年艺术家，以及正在接受文化传承的青年学子，从中我们能充分展现艺术传承和艺术传播，艺术精神的传承是如何一代又一代影响下去的。也提倡还在学习的青年学子积极参与，为他们提供自我展现的平台和与独立艺术家沟通交流机会，为学生建立沟通的桥梁。
        </p>
        <h5>(二) 项目背景、意义、实施必要性</h5>
        <p>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;项目背景：习中国特色社会主义进入新时代，习近平同志在十九大报告中强调，中国特色社会主义进入新时代，我国社会主要矛盾已经转化为人民日益增长的美好生活需要和不平衡不充分的发展之间的矛盾。文艺是时代前进的号角，最能代表一个时代的风貌，最能引领一个时代的风气。近年来，党和国家不断加大对文艺创作的扶持力度，让文艺工作者更有底气，更有信心。国家对文艺重视力度不断加大，我们希望在新时代的文艺发展中贡献自己的一份新思路
        </p>

        <p>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;意义、实施必要性：党的十九大报告的主题是不忘初心，牢记使命，前者“不忘初心”我认为亦是一种文化传承，不抛弃传统艺术，不丢掉艺术之根源。我们希望当下艺术家及学子能不忘初心，牢记使命，在文艺的浪潮中不要迷失自我，文艺家要将对历史进程的理解，对民族命运的思考和对人生、人性的开掘有机地融合到自己的创作之中，争取从博大精深的民族文化中汲取养分，将特定的文化内蕴融入作品中。其次，展览方式仍旧停留在实体美术馆展览，我们有一个新思路：在快节奏的生活上，给予文艺作品一个线上平台，也为那些不能远道而行的人准备一个线上展览。
        </p>
        <h5>（三） 国、内外研究现状</h5>
        <p>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;国内研究现状和发展动态：当今社会快节奏发展，人们对传统文化的抛弃，狂热般的追赶新型事物，文艺创作手法思路也与以往不同，我们希望在这样一个展览平台能搭建一个老一辈和新生艺术家、学子的沟通交流平台，实现文艺交流传承；在快节奏的生活上，追求经济物质的道路上，会忽视精神文化上的需求。我们翻看国内网页，未看到一个集成各博物馆、美术馆的线上展览的网站，线上展览更多的是独立出现在各博物馆官方网页，所以我们希望能在这一方面做出一定的突破。
        </p>
        <p>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;国外研究现状和发展动态：当今世界全球化，对于国内外展览的整合资源缺少，我们对于西方的画作展览信息，了解得少之又少，如果后期有能力，希望不仅是做国内线上展览，也能加入国外的展览讯息及动态。
        </p>
        <h5>（四） 创新点与项目特色</h5>
        <p>
          1、美术馆是一个实体参观，有很多因为距离远，时间不够的人都不能在那个时间段去参观展览而错过，为那些不能远道而行的人准备一个线上展览。创作一个线上的网页平台、微信推送，能用手机，电脑等电子产品进行参观。线上展览它包括了：艺术家介绍，作品简介，以及作品细节的照片等。观展者看见喜欢的作品可以和作者联系，和对方进行了解。
        </p>
        <span className="mb-3">
          后续衍生：通过微信推送，网站形式，我们可以定期推送一些作品，推送还在上学的学生的作品，及其他们作品创作的想法，也算是给现在还在学习想走上艺术道路的学生一个平台，一个推送渠道。并希望后续做成一个美术馆、博物馆的线上展览集成。
        </span>
        <p className="mt-3">
          2、邀请老退伍军人，老红军来观展，（这个可以具体实施，可以走街串巷去找找。）从作品中，既可以缅怀过去，也可以让他们展望未来。既体现了我们的主题与观念，也是一种观展人群的创新，体现艺术人文的关怀。
        </p>
      </div>
    </div>
  );
};

export default Plan;



