import React from 'react';
import classnames from 'classnames';

const PersonNavName = ({ name, nameActive, onGalleryNameClcik }) => {
  return (
    <div id="PersonNavName">
      <a
        href="#"
        className={classnames(
          'px-4 border-right border-secondary text-muted d-block',
          { 'border-white': nameActive }
        )}
        onClick={e => {
          onGalleryNameClcik(e, name);
        }}
      >
        <div className={classnames({ 'text-white': nameActive })}>{name}</div>
      </a>
    </div>
  );
};

export default PersonNavName;



