import React, { Component } from 'react';
import PersonNavName from '../common/PersonNavName';
import NavNameMb from '../common/NavNameMb';
import GalleryImgGroup from '../common/GalleryImgGroup';

class Gallary extends Component {
  constructor(props) {
    super();
    props.changeNavActive('作品');
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    const {
      name,
      indexMap,
      nameActive,
      onGalleryNameClcik,
      persons
    } = this.props;
    const personArr = persons.filter(person => {
      return person.name === name;
    });
    const person = personArr[0];
    console.log(personArr);
    return (
      <div>
        <section id="gallery-list" className="text-white">
          <div className="container bg-gallery-dark gallery-mt gallery-caption">
            <h1 className="p-4 m-0 ">艺术作品</h1>
          </div>
          <div className="container bg-gallery-dark gallery-caption">
            <div
              id="gallery-caption-pc"
              className="d-flex justify-content-between p-3 border-bottom border-secondary"
            >
              <div>人物</div>
              <div className="d-flex">
                {persons.map(person => (
                  <PersonNavName
                    key={person.name}
                    name={person.name}
                    nameActive={person.nameActive}
                    onGalleryNameClcik={onGalleryNameClcik}
                  />
                ))}
              </div>
            </div>

            <div
              id="gallery-caption-mb"
              className="d-flex justify-content-between p-3 border-bottom border-secondary"
            >
              <div id="accordion" className="w-100">
                <div className="card bg-gallery-dark">
                  <div className="card-header">
                    <h5>
                      <a
                        className="d-block"
                        href="#collapse1"
                        data-parent="#accordion"
                        data-toggle="collapse"
                      >
                        <div className="d-flex justify-content-between">
                          <h4>人物</h4>
                          <div>
                            <i className="fas fa-angle-down" />
                          </div>
                        </div>
                      </a>
                    </h5>
                  </div>
                  <div id="collapse1" className="collapse show">
                    <div className="card-body text-center">
                      {persons.map(person => (
                        <NavNameMb
                          key={person.name}
                          name={person.name}
                          nameActive={person.nameActive}
                          onGalleryNameClcik={onGalleryNameClcik}
                        />
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div className="pt-4 text-center">
              {name}
              作品集
            </div>

            {indexMap.map(index => (
              <GalleryImgGroup
                key={index}
                person={person}
                name={name}
                index={index}
              />
            ))}
          </div>
        </section>
      </div>
    );
  }
}

export default Gallary;



