import React, { Component } from 'react';

class Display extends Component {
  constructor(props) {
    super();
    props.changeNavActive('线下展览');
  }
  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div>
        <img
          src={require('../../img/displayHD.jpg')}
          alt=""
          className="img-fluid w-100 py-5"
        />
        <div className="container">
          <p>
            {'  '}
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2018年8月31日下午3点，“破壁点津——九人书画联展”在湖南省藏福美术馆顺利举行，此次展览由中国美术学院、湖南省美术家协会、湖南省画院主办，由湖南省藏福美术馆承办，由留芳书画交流中心协办。
          </p>
          <p>
            {' '}
            {'  '}
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            此次展览作为中国美术学院“不忘初心，一脉相承”主题的返乡展，由中国美术学院孔令伟老师、徐增鎏老师指导，视觉文化与艺术管理专业李思诗策展。本次展览有幸展出湖南省画院副院长田绍登、湖南省青年画院副院长付红，及美院学子——王子豪、杨任伟、张鹏、胡维桢、段美婷、高泽、黄己榕的书法与山水作品。展览的主题“破壁点津”就是旨在破除不同年龄、不同身份的界限，让艺术家们保持活性、让青年学子吸收营养
          </p>
          <p>
            {' '}
            {'  '}
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;参观此次展览的领导和嘉宾有：湖南省政协副主席、湖南省文联主席欧阳斌；湖南省宣传部副部长杨金鸢；湖南省新闻出版广电局副局长毛良才；长沙市宣传部副部长赵柏林；湖南省政协常委、省美协副主席段江华；湖南省美协副主席、省画院副院长石刚；湖南省美协副主席、省工笔画协会会长吴荣光；湖南省书协副主席兼秘书长胡紫桂等。
          </p>
          <p>
            {' '}
            {'  '}
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;此次展览旨在把“点津”的过程搬到展览现场，举行了一场学术沙龙，使展览除具备传统性质上的观看意义同时，更呈现出一个如何交流，如何破壁的实时过程。在此次学术沙龙中，美院中国书画理论专业高泽作为学术主持，介绍此次展览主题思路，并确定此次沙龙主题——传统艺术如何体现当代性？学子们全方位地介绍了自己的学习经历和学术成果，艺术家段江华、吴荣光、田绍登、付红就学子在书画实践中的问题抽丝剥茧、引经据典，进行回答，指出开阔的视野和包容的心态是使所学知识切入当代的关键。
          </p>
          <p>
            {'  '}
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;“桃源何处是，游子正迷津。”此次展览更是一次生动的公开课，使迷津学子驶出困惑，得到切实提升的同时，更体现出作为艺术家对年轻学子的历史责任感与来自与同一块土地的关怀。自十九大以来，文化自信的重要性多次被重提，要坚持走文化自信的道路，坚守艺术理想，勇于创新创造。这对广大的艺术工作者来说，是机遇，更是挑战。“文变染乎世情，兴废系乎时序”别处即此处，他时即此时，这就是艺术创作最好的时代。
          </p>

          <p>
            {'  '}
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            展览呈现在湖南省藏福美术馆，据悉将持续至2018年9月6日，欢迎广大书画爱好者前来观展
          </p>

          <h2 className="text-center p-4"> 展览现场</h2>
          <div className="row">
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display1.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display2.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display3.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display4.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
          </div>
          <div className="row">
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display5.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display6.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display7.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display8.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
          </div>

          <div className="row pb-4">
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display9.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display10.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display11.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
            <div className="col-md-3 pb-3">
              <img
                src={require('../../img/display12.jpg')}
                alt=""
                className="img-fluid"
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Display;



