import React from 'react';
import classnames from 'classnames';
import { Link } from 'react-router-dom';

const NavbarLinkGroup = ({ nav }) => {
  return (
    <div>
      <li className={classnames('nav-item', { active: nav.isActive })}>
        <Link to={nav.link} className="nav-link">
          {nav.name}
        </Link>
      </li>
    </div>
  );
};

export default NavbarLinkGroup;



