import React from 'react';
import classnames from 'classnames';

const ArtistIfo = ({ name, persons }) => {
  const personIndex = persons.find(person => person.name == name);
  const person = personIndex;
  const isHaveExp = person.exp == false;
  const isHaveThoughts = person.thoughts == '';
  return (
    <div className="col " id="artWorks">
      <div className="col-md-4 m-auto">
        {' '}
        <img
          src={require(`../../img/person/${name}.jpg`)}
          alt=""
          className="img-fluid rounded-circle m-auto"
        />
      </div>
      <h3 className="pt-4 text-center">
        {name}
        简介
      </h3>
      <div className="col-md-6 m-auto">
        <p className="text-muted">{person.info}</p>
        <div className={classnames({ 'd-none': isHaveExp })}>
          <h3 className="pt-4 text-center">个人经历</h3>
          <div className="text-muted pt-3">
            {person.exp.map(exp => {
              return (
                <div key={exp}>
                  <span>{exp}</span>
                  <br />
                </div>
              );
            })}
          </div>
        </div>
        <div className={classnames({ 'd-none': isHaveThoughts })}>
          <h3 className="pt-4 text-center">个人感想</h3>
          <div className="text-muted py-3"> {person.thoughts}</div>
        </div>
      </div>
    </div>
  );
};

export default ArtistIfo;



