import React, { Component } from 'react';
import PersonImgGroup from '../common/PersonImgGroup';
import CardProductGroup from '../common/CardProductGroup';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import { Link } from 'react-router-dom';

class Landing extends Component {
  state = {};

  constructor(props) {
    super();
    props.changeNavActive('主页');
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    const { persons, onGalleryNameClcik } = this.props;
    return (
      <div id="home">
        <section id="showcase">
          <div id="myCarousel" className="carousel slide" data-ride="carousel">
            <ol className="carousel-indicators">
              <li
                data-target="#myCarousel"
                data-slide-to="0"
                className="active"
              />
              <li data-target="#myCarousel" data-slide-to="1" />
            </ol>
            <div className="carousel-inner">
              <div className="carousel-item order2 carousel-image-2 active">
                <div className="">
                  <div className="container">
                    <div className="carousel-caption">
                      <Link to="/plan2" className="btn btn-info mb-3">
                        了解更多
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
              <div className="carousel-item carousel-image-1">
                <div className="">
                  <div className="container">
                    <div className="carousel-caption">
                      <Link to="/plan" className="btn btn-danger mb-3">
                        了解更多
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <a
              href="#myCarousel"
              data-slide="prev"
              className="carousel-control-prev"
            >
              <span className="carousel-control-prev-icon" />
            </a>
            <a
              href="#myCarousel"
              data-slide="next"
              className="carousel-control-next"
            >
              <span className="carousel-control-next-icon" />
            </a>
          </div>
        </section>

        {/* Home Icon Section */}

        <section id="home-icon" className="py-5">
          <div className="container">
            <div className="row">
              <div className="col-md-4 mb-2 text-center">
                <AnchorLink href="#home-heading-section">
                  <i className="fas fa-palette fa-3x pb-2" />
                  <h3>作品</h3>
                  <p className="text-muted">艺术作品展示</p>
                </AnchorLink>
              </div>
              <div className="col-md-4 mb-2 text-center">
                <AnchorLink href="#person-heading-section">
                  <i className="fas fa-users fa-3x pb-2" />
                  <h3>艺术家及学子</h3>
                  <p className="text-muted">艺术家及学子简介</p>
                </AnchorLink>
              </div>
              <div className="col-md-4 mb-2 text-center">
                <AnchorLink href="#display-heading-section">
                  <i className="fas fa-tv fa-3x pb-2" />
                  <h3>线下展览</h3>
                  <p className="text-muted">艺术作品线下展示</p>
                </AnchorLink>
              </div>
            </div>
          </div>
        </section>

        {/* Home Heading Section */}

        <section
          id="home-heading-section"
          className="p-5 text-white text-center"
        >
          <div className="row">
            <div className="col">
              <div className="container pt-5">
                <div className="row">
                  <div className="col-md-6 m-auto">
                    <h1>艺术作品</h1>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* Works Section */}

        <section id="info" className="py-5">
          <div className="container">
            <div className="card-columns">
              {persons.map(person => (
                <CardProductGroup
                  onGalleryNameClcik={onGalleryNameClcik}
                  name={person.name}
                  route={person.route}
                  key={person.name}
                />
              ))}
            </div>
          </div>
        </section>

        {/* Person Heading Section */}

        <section
          id="person-heading-section"
          className="p-5 text-white text-center"
        >
          <div className="row">
            <div className="col">
              <div className="container pt-5">
                <h1>艺术家及学子</h1>
              </div>
            </div>
          </div>
        </section>

        {/*person section  */}

        <section id="person" className="py-5 text-white text-center">
          <div className="container">
            <div className="row">
              {persons.map(person => (
                <PersonImgGroup
                  onGalleryNameClcik={onGalleryNameClcik}
                  name={person.name}
                  route={person.route}
                  key={person.name}
                />
              ))}
            </div>
          </div>
        </section>

        {/* display Heading Section */}

        <section
          id="display-heading-section"
          className="p-5 text-white text-center"
        >
          <div className="row">
            <div className="col">
              <div className="container pt-5">
                <h1>线下展览</h1>
              </div>
            </div>
          </div>
        </section>

        {/* display Section */}

        <section id="display-info" className="py-3">
          <div className="container">
            <div className="row">
              <div className="col-md-6 text-left align-self-center py-3">
                <p>主办单位：中国美术学院 湖南省美术家协会 湖南省画院</p>
                <p>承办单位：湖南省藏福美术馆</p>
                <p>展览时间： 2018年8月31号 — 9月6号</p>
                <div className="ml-5 pl-5 pt-4 display-mb-margin">
                  <Link to="/display" className="btn btn-outline-danger">
                    阅读更多
                  </Link>
                </div>
              </div>
              <div className="col-md-6">
                <img
                  src={require('../../img/display.jpg')}
                  className="img-fluid"
                  alt=""
                />
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default Landing;



